.page {
  padding: 0;
  margin: 0 auto;
  height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  background-color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .page {
    min-width: 320px;
  }
}
@media (max-width: 767px) {
  .page-main {
    padding-top: 70px;
  }
}
h1,
h2,
h3 {
  font-weight: inherit;
  padding: 0;
  margin: 0;
}
p {
  padding: 0;
  margin: 0;
}
* {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: fixed;
  transform: scale(0);
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.page-header {
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding-left: 110px;
    padding-right: 110px;
    background-color: transparent;
  }
}
.page-header__wrapper {
  padding-top: 10px;
  padding-bottom: 17px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .page-header__wrapper {
    border-bottom: 2px solid #D9D9D9;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header-logo {
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  justify-content: space-between;
}
.header-logo:hover {
  opacity: 0.8;
}
.header-logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .header-logo {
    padding-left: 0;
    padding-right: 0;
  }
}
.header-logo__button {
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  min-width: 26px;
  min-height: 24px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-mobile-menu.svg");
  background-position: center;
}
.header-logo__button--close {
  background-image: url("../img/icon-close.svg");
}
@media (min-width: 768px) {
  .header-logo__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav {
    display: none;
  }
  .main-nav--open {
    display: block;
    position: absolute;
    width: 100%;
    top: 100%;
    background-color: #ffffff;
  }
}
.main-nav__list {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  color: #000000;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    min-width: 440px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-nav__list {
    font-size: 20px;
    line-height: 30px;
    min-width: 478px;
  }
  .main-nav__list--index-desk {
    color: #ffffff;
  }
}
.main-nav__item {
  text-decoration: none;
}
.main-nav__item:not(.main-nav__item--current):hover {
  opacity: 0.6;
}
.main-nav__item:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav__item {
    padding-top: 22px;
    padding-bottom: 22px;
    border-bottom: 2px solid #D9D9D9;
    display: flex;
    justify-content: center;
  }
}
.main-nav__item a {
  text-decoration: inherit;
  color: inherit;
}
@media (min-width: 768px) {
  .main-nav__item--current {
    border-bottom: 3px solid #68B738;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--index-desk .main-nav__item--current {
    border-bottom: 3px solid #ffffff;
  }
}
