.title-wrapper-bg-none {
  padding: 40px 20px 45px 20px;
}
@media (min-width: 768px) {
  .title-wrapper-bg-none {
    padding-left: 70px;
    padding-right: 70px;
    padding-top: 130px;
    padding-bottom: 29px;
  }
}
@media (min-width: 1440px) {
  .title-wrapper-bg-none {
    padding-left: 110px;
    padding-right: 110px;
  }
}
.title-wrapper-bg-none__title {
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .title-wrapper-bg-none__title {
    font-size: 60px;
    line-height: 60px;
  }
}
.title-wrapper-bg-none__text {
  margin-top: 37px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .title-wrapper-bg-none__text {
