.button {
  background-color: #68B738;
  color: #ffffff;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  border: none;
  font-family: "Oswald", sans-serif;
  padding-top: 10px;
  padding-bottom: 10px;
  text-decoration: none;
  text-align: center;
}
.button:hover {
  background-color: #5EAA2F;
}
.button:active {
  background-color: #5EAA2F;
  color: rgba(255, 255, 255, 0.3);
}
