.task {
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .task {
    padding-left: 30px;
    padding-right: 30px;
    gap: 30px;
    margin-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .task {
    padding-left: 110px;
    padding-right: 110px;
    flex-direction: row;
    gap: 80px;
    margin-bottom: 70px;
  }
}
.task__block {
  background-color: #F2F2F2;
  padding: 30px 20px 18px 20px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .task__block {
    padding: 53px;
  }
}
.task__title-wrapper {
  margin-bottom: 29px;
  display: flex;
  gap: 19px;
  align-items: center;
  position: relative;
}
.task__black-cat {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #68B738;
  position: relative;
}
.task__black-cat::before {
  content: "";
  display: inline-block;
  position: absolute;
  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  width: 36px;
  height: 50px;
  top: -10%;
  left: 10%;
}
@media (min-width: 768px) and (max-width: 1339px) {
  .task__black-cat::before {
    width: 140px;
    height: 194px;
  }
}
.task__black-cat--pro::before {
  background-image: url("../img/icon-cat-pro.svg");
  width: 67px;
  height: 28px;
  top: 25%;
  left: -25%;
}
@media (min-width: 768px) and (max-width: 1339px) {
  .task__black-cat--pro::before {
    width: 288px;
    height: 112px;
  }
}
@media (min-width: 768px) and (max-width: 1339px) {
  .task__black-cat {
    position: absolute;
    width: 200px;
    height: 200px;
    top: 25%;
    right: 0;
  }
}
.task__title {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
}
@media (min-width: 768px) {
