  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .task__text {
    border-bottom: none;
    margin-bottom: 48px;
    padding-bottom: 0;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 768px) and (max-width: 1339px) {
  .task__text {
    width: 57%;
  }
}
@media (min-width: 1440px) {
  .task__text {
    margin-bottom: 23px;
  }
}
.task__link {
  text-transform: uppercase;
  font-size: 16px;
